.container {
  width: 400px;
  height: 400px;
  border: 2px solid red;
  position: relative;
  top: 0;
  left: 40%;
}
.container #x-axis {
  box-sizing: border-box;
  width: 400px;
  /* height: 10px;; */
  border: 2px dashed black;
  position: absolute;
  top: 199px;
  z-index: 10;
}
.container #y-axis {
  box-sizing: border-box;
  /* width:400px; */
  height: 400px;
  border: 2px dashed black;
  position: absolute;
  left: 199px;
  z-index: 10;
}
#arrow {
  max-width: 300px;
  min-width: 200px;
  height: 25px;
  position: absolute;
  left: 50%;
  top: 239px;
  z-index: 12;
  zoom: 80%;
  transform-origin: left;
}
.container #x-axis--label{
  position: absolute;
  left:390px;
  top:180px;
  overflow: hidden;
}
.container #-x-axis--label{
  position: absolute;
  left:3px;
  top:180px;
  overflow: hidden;
}
.container #y-axis--label{
  position: absolute;
  left:205px;
  top:3px;
  overflow: hidden;
}
.container #-y-axis--label{
  position: absolute;
  left:205px;
  top:380px;
  overflow: hidden;
}
.container #origin--label{
  position: absolute;
  left:205px;
  top:180px;
  overflow: hidden;
}
