  .title{
    color:rgb(255,255,255,0.8);
    font-size:10rem;
    letter-spacing:3rem;
    font-family: 'Rajdhani', sans-serif;
    text-shadow: 0px 0px 20px yellow, 0px 0px 40px cyan, 0px 0px 60px cyan,0px 0px 80px cyan, 0px 0px 100px cyan;
  }
#axes-container {
    position: relative;
    margin-top:150px;
}
#axes-container .button {
  width: 75px;
  height: 35px;
  margin-top: 15px;
  background-color: aqua;
  border:none;
  border-radius: 8px;
  font-weight: 600;
  color: orangered;
}
#axes-container .button:hover ,
#axes-container .button:active {
  background-color: orangered;
  font-weight: 500;
  color: aqua;
}

